*{
  padding: 0;
  margin: 0;
  line-height: 1;
  box-sizing: border-box;
}
body{
  font-family: Federant;
}
.container{
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  padding-top: calc((100vh - 816px)/2);
}
#header,#main{
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: .625rem;
  background-color: white;
  height: 816px;
  box-shadow: 0px 0px 4px gray;
}
#header{
  margin-right: 1.5rem;
  width: 360px;
}
#main{
  width: 1200px;
}
header,main{
  height: 100%;
}
header{
  width: 100%;
  padding: 1rem .7rem;
}
main::-webkit-scrollbar {
  display: none;  /* Ocultar scroll */
}
main{
  padding: 5px;
  display: flex;
  margin: auto;
  width: 100%;
  overflow: hidden;
  overflow-y: scroll;
  justify-content:space-around;
  flex-wrap: wrap;
}
.top, .bottom{
  z-index: 5;
  width: 100%;
  height: 2rem;
  max-height: 2rem;
  box-shadow: 0px 0px 5px grey;
}
.top{
  padding: 2px .3rem 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.top a{
  text-decoration: none;
  /* font-variant: small-caps; */
  height: 1rem;
}
.user{
  display: flex;
  width: 100%;
  padding: 0 1rem .7rem;
}
.userInfo{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.userInfo p{
  font-variant: small-caps;
  line-height: normal;
}
.user .imagenperfil{
  /* border-radius: 50%; */
  transform: rotateZ(45deg);
  overflow: hidden;
  margin-right: .4rem;
  width: 3.5rem;
  height: 3.5rem;
  border: 1px solid rgb(30, 144, 255,.5);
  box-shadow: 0px 0px 2px dodgerblue;
}
.user .imagenperfil img{
  height: 100%;
  transform: rotateZ(-45deg);
  margin-top: 1px;
  margin-left: -.5rem;
}
/* Posts */
li{
	list-style: none;
}
article:nth-child(odd) {
	background-image: url(https://creik.github.io/IndexAnime/background1.jpg);
}
article:nth-child(even) {
	background-image: url(https://creik.github.io/IndexAnime/background2.jpg);
}
article {
	position: relative;
	align-self: stretch;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	/* border-radius: .625em; */
	/* border: 1px solid orange; */
	background-size: cover;
  background-repeat: no-repeat;
	background-position: center;
	color: white;
	/* flex: 1 18.75em; */
	/* min-width: 25.95em; */
	max-width: 23em;
	/* max-height: 15em;
	min-height: 15em; */
	max-height: 14em;
	min-height: 14em;
	box-sizing: content-box;
	margin: .5rem 0;
	padding: .3em .5em;
	box-shadow: 0px 0px 5px rgba(0,0,0,0.3);
}
article .title h4 {
    font-size: 1.5em;
    color: white;
    text-shadow: 1px 0px 1px rgba(0,0,0,.7), 0px 1px 1px rgba(0,0,0,.7), -1px 0px 1px rgba(0,0,0,.7), 0px -1px 1px rgba(0,0,0,.7);
}
article .info ul {
    display: flex;
    color: white;
    justify-content: space-around;
}
article .info ul li {
    position: relative;
    padding: .2em;
    font-weight: bold;
    /* text-transform: uppercase; */
    text-shadow: 1px 0px 1px rgba(0,0,0,.7), 0px 1px 1px rgba(0,0,0,.7), -1px 0px 1px rgba(0,0,0,.7), 0px -1px 1px rgba(0,0,0,.7);
}
article .tec {
    visibility: hidden;
    position: absolute;
    margin: auto;
    right: .8em;
    top: .5rem;
    margin-top: -.5em;
    /* transition: all 10s linear; */
}
article .tec ul li {
    margin: .7em 0;
    line-height: 1;
    font-weight: bold;
    text-shadow: 1px 0px 1px rgba(0,0,0,.7), 0px 1px 1px rgba(0,0,0,.7), -1px 0px 1px rgba(0,0,0,.7), 0px -1px 1px rgba(0,0,0,.7);
}



/* HEADER */
legend{
  margin-left: .3rem;
}
fieldset{
  border-left: none;
  border-right: none;
  border-bottom: none;
  border-top: .5px solid gray;
}
ul{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
#abc {
  padding: .5rem 0 .3rem;
  height: 75px;
}
#abc li{
  width: 1.6rem;
  text-align: center;
  font-size: 1.5rem;
}
#abc li:hover{
  transform: scale(1.3);
}
#abc label{
  display: block;
  pointer-events: none;
}
#abc label input{
  position: absolute;
  visibility: hidden;
}
input[type=checkbox]{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding: -.2rem;
  visibility: hidden;
}
.active{
  transform: scale(1.3);
}
#genders label{
  position: relative;
  pointer-events: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}
#genders{
  justify-content: center;
}
#genders li{
  margin: .2rem;
  padding: .2rem;
  border: 1px solid orange;
}
input[type=search]{
  outline: none;
  margin: .5rem auto;
  padding: .2rem;
  display: block;
  border: 1px solid gray;
  width: 70%;
  /* border-radius: .3rem; */
}

.filters{
	padding: .7rem 0;
}
.filters ul{
  display: flex;
  /* justify-content: space-around; */
}
.filters select{
  margin: .2rem;
	position: relative;
	border-color: dodgerblue;
  padding: .2em;
  font-size: 1em;
  width: auto;
	font-family: Federant;
	outline: none;
}
.flters select option {
	color: black;
	background-color: white;
}
.filters li{
  position: relative;
}
button[class*="reset"]{
  position: absolute;
  display: none;
  top: 0;
  right: 2px;
  height: 100%;
  border: none;
  border-top: 1px solid dodgerblue;
  border-bottom: 1px solid dodgerblue;
  width: 1rem;
  background-color: white;
  outline: none;
}


/* Version 1366px x 768px */

@media screen and (max-width: 1366px){
  body{
    overflow: hidden;
  }
  .container{
    /* padding-top: 5vh; */ /*first version*/ 
    padding-top: calc((100vh - 668.16px)/2);
  }
  #header,#main{
    border: none;
    /* height: 90vh; */ /*first version*/
    height: 668.16px;
  }
  #abc li{
    width: 1.3rem;
    font-size: 1.2rem;
  }
  #header{
    width: 300px;
    margin-right: 1rem;
  }
  #main{
    width: 950px;
  }
  article{
    min-width: 18em;
    max-width: 18em;
    max-height: 11em;
    min-height: 11em;
  }
  .info ul li:nth-child(5),.info ul li:nth-child(6){
    display: none;
  }
  #genders li:nth-child(17),#genders li:nth-child(16),
  #genders li:nth-child(15){
    display: none;
  }
}






